<template>
    <div>
        <div class="search">
            <input type="text" class="search-input" placeholder="Search in annotations" v-model="filter">        
        </div>
        <ul class="search-list">
            <li v-if="selectedItems.length == 0">No Sbol component found</li>
            <li 
                v-for="(item, index) in selectedItems" 
                :key="index"
                @click="detailItem(item.index)" 
                >
                <div>
                    <ul>
                        <li><b>Sequence Ontology:</b> <span>{{ item.SBOL }} </span></li>
                        <li><b>Name:</b> <span>{{ item.name }} </span></li>
                        <li><b>Direction: </b> <span>{{ item.direction }} </span></li>
                    </ul>
                </div>
                <span>
                    {{ item.start }}…{{ item.end }}
                </span>                                
            </li>
        </ul>
    </div>      
</template>
<script>
export default {
    /*
    
        :class="{ active: hover }"
        @mouseover="hover = true"
        @mouseleave="hover = false"
    
    */

    props: ["annotations"],
    data() {
        return {
            filter: ""
        }
    },
    computed: {
        selectedItems() {
            // let filters = [];            
            return this.annotations.filter(so => {
                if (this.filter === "") {                
                    return so;
                }                               
                return so.name.toLowerCase().includes(this.filter.toLowerCase()) || 
                    so.start.toString().includes(this.filter.toString()) || 
                    so.SBOL.toString().includes(this.filter.toString()) || 
                    so.end.toString().includes(this.filter.toString()) || 
                    so.direction.toLowerCase().includes(this.filter.toString());                
            });
        },        
    }, 
    methods: {
        detailItem(so) {
            window.console.log("detailItem from ListAnnotations", this.annotations[so]);
            this.$emit('selectedAnnotation', so)
        }
    }
}
</script>
